@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap);
html, body{
  margin:0;
  padding:0;
  height:100vh; /* needed for container min-height */  
  font-family: 'Montserrat', sans-serif;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  background-color: #353B3F;
  scroll-behavior: smooth;
}


h1,h2,h3,p{
  margin: 0px;
  margin :15px 0;
  color: #4B4B4B; 
}
a:link { text-decoration: none; }
h1{
  font-size: 80px;
  font-weight: 500;
}

h3{
  font-size: 15px;
  font-weight: 150;
}
p {
  text-align: justify;
  padding-top: 10px;
  font-size: 0.95rem;
  line-height: 150%;
}
.container{
  display: flex;
}
.hero{
  display: flex;
  background-image: none;
  min-height: 95vh;
  justify-content: center;
  display: flex;
}
.hero-card{
  display: flex;
  background-color:#f9f7f6;
  align-self: center;
  padding: 30px;
  min-height: 400px;
  border-radius: 1em;
  flex-direction: column;
  max-width: 45%;
  box-shadow: 10px 10px 93px 0px rgba(0, 0, 0, 0.75);
  /* margin: 200px auto; */

  /* justify-content: stretch; */
}
.search-form{
  display: flex;
  align-items: center;
}

.combobox{
  display: flex;
  flex:auto;
  /* width: 350px ; */
}

input,
.comboinput{
  padding: 12px 4px ;
  margin: 0 1px;
  border-radius: 0.25rem;
  border:1;
  flex:auto;
  /* width: 350px ; */
}

.result{
  display: flex;
  flex: 1 1;
  min-height: 90vh;
}
.loader{
  margin: auto;
  align-self: auto;
}
.main-footer {
  display: flex;
	background: #d9e4ea;
	padding: 1.5em 2em;
	margin-top: 30px;
  font-size: smaller;
}
.footer-inner{
  max-width: 50%;
  justify-content: center;
  align-self: center;
  
}
.socials {
  /* display: flex; */
  /* margin-right: 10px; */
  justify-content: center;
  align-items: center;
}
.socials > li {
  margin: 0 20px;
}

button{
  align-self:stretch;
  /* margin-left: 20px; */
  padding: 4px;
  border:0;
  border-radius: .75em;
}
.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid #3498db; /* Blue */
  border-radius: 50%;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

/* The card */
.card {
  display: flex;

  position: relative;
  height: 450px;
  max-width: 45%;
  width: 900px;
  padding: 30px;
  margin: 100px auto;
  background-color: #f9f7f6;
  border-radius: 1em;
  box-shadow: 10px 10px 93px 0px rgba(0, 0, 0, 0.75); 
}

img{
  display: block;
  max-width:360px;
  max-height:360px;
  width: auto;
  height: auto;
  object-fit: cover;
  box-shadow: 10px 10px 60px 0px rgba(0, 0, 0, 0.75);
}
.left{
margin-top: 30px;
}
/* Right side of the card */
.right {
  margin-left: 40px;
  margin-right: 20px;
  flex: auto; }

.business-name { 
  padding-top: 15px;
  font-size: 1.5rem;
  color: #4B4B4B; 
}
.top{
  justify-content: space-between;
}
.address{
  display: flex;
  flex-direction: column;
  justify-content: right;
}
.address-line{
  font-size: x-small;
  margin: 0;
  padding: 0;
  justify-self: right;
}
.separator {
  margin-top: 10px;
  border: 1px solid #C3C3C3; 
}
.points{
  font-size: 22px;
  font-weight: 200;
}
.gallery{
  justify-content: space-between;
  display: flex;
  margin-top: 20px;
  
}
.thumbnail{
  display: block;
  max-width:100px;
  max-height:100px;
  width: auto;
  height: auto;
}
/* Floating action button */
.fab {
  position: absolute;
  left: auto;
  top: auto;
  padding-top: 18px;
  box-sizing: border-box;
  background-color: #1875D0;
  width: 80px;
  height: 80px;
  color: white;
  text-align: center;

  border-radius: 15%;
  box-shadow: 10px 10px 50px 0px rgba(0, 0, 0, 0.75); }

